.wrapper{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.wrapper button{
    width: 35px;
    height: 35px;
    border: transparent;
    padding: 0 3px;
    border-radius: 2px;
    color: #ffffffa8;
    background-color: transparent;
    z-index: 1;
    align-self: self-end;
    transition: border 700ms;
}
.wrapper button:hover{
    border: 1px solid #b71111a3;
}
.image{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 0;
}

.head{
    width: 100%;
    height: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: end;
    z-index: 1;
    background-color: transparent;
    
}
.head h3{
    color: #ffffff;
    font-size: 30px;
    text-align: center;
    margin: 20px 0;
}
.head p{
    color: #ebeaeaf3;
    border: 1px solid #ffffff75;
    border-radius: 10px;
    text-align: center;
    padding: 2px;
    width: 80px;
}
.read{
    width: 100%;
    height: 35px;
    background-color: transparent;
    color: white;
    border: 1px solid #b71111;
    border-radius: 10px;
    transition: background-color 500ms;
    padding: 5px;
    text-align: center;
    text-decoration: none;
}
.read:hover{
    background-color: #b71111b6;
    border: none;
}